  opacity: 0;
  pointer-events: none;
  visibility: visible;
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(1.5);
}

.modal {
  width: calc(100% - 40px);
  z-index: 5000;
  text-align: center;
  background-color: #fcfcfc;
