  position: relative;
  top: 50%;
  left: 50%;
  overflow-x: hidden;
  overflow-y: auto;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 3px 10px -.5px rgba(0, 0, 0, .2);
}

